.navContainer {
    position: fixed;
    width: 100%;
    z-index: 10;
    margin-top: -300px;
}

.navContainer .navContent {
    width: 100%;
    flex-direction: column;
    display: flex;
    justify-content: center;
    height: 100px;
    align-items: center;
}

.navContainer #navMenuPlus {
    flex-direction: column;
    background: #0e111b;
    width: 100%;
    padding: 22px;
    z-index: 1;
}

.navContainer .default {
    display: none;
}

.navContainer #navMenuPlus li {
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 20px;
    margin-top: 20px;
    color: white;
    transition: all ease-in-out 0.3s;
}

.navContainer .navMenu {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(180, 180, 180);
}

.navContainer .arrow {
    transform: rotate(90deg);
    width: 20px;
    display: none;
}

.navContainer .navMenu li {
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 40px;
    margin-left: 50px;
    margin-right: 50px;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transition: all ease-in-out 0.3s;
}

.navContainer .navMenu li:hover {
    color: white;
}

.navContainer .navMenu li::after {
    content: '';
    display: flex;
    width: 100%;
    height: 3px;
    border-radius: 5%;
    background-color: #FFC25C;
    opacity: 0;
    margin-top: 10px;
    transition: all ease-in-out 0.3s;
}

.navContainer .navMenu li:hover::after {
    opacity: 1;
    margin-top: 0;
}
@media screen and (max-width: 1300px) {
    .navContainer .navMenu li {
        font-size: 30px;
    }
}

@media screen and (max-width: 1000px) {
    .navContainer .navMenu li {
        font-size: 20px;
        margin-left: 30px;
        margin-right: 30px;
        opacity: 1;
    }
}

@media screen and (max-width: 700px) {
    .navContainer {
        margin-top: -100px;
    }

    .navContainer .navMenu li {
        font-size: 15px;
        margin-left: 20px;
        margin-right: 20px;
    }
}

@media screen and (max-width: 550px) {

    .navContainer {
        margin-top: -400px;
        height: 400px;
    }

    .navContainer .navMenu li {
        display: none;
    }

    .navContainer .arrow {
        display: block;
    }

    .navContainer .navContent {
        flex-direction: column;
        justify-content: center;
        height: 100%;
        /* height: 70px; */
    }

    .navContainer #navMenuPlus {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .navContainer #arrow {
        margin-top: 20px;
    }
/* 
    .navContainer .default {
        margin-top: -130px;
    } */
}
.navContainer .open {
    z-index: 1;
    transform: rotate(0deg);
    -webkit-animation: rotate-open 1s forwards;
            animation: rotate-open 1s forwards;
}

.navContainer .close {
    z-index: 1;
    transform: rotate(180deg);
    -webkit-animation: rotate-close 1s forwards;
            animation: rotate-close 1s forwards;
}

@-webkit-keyframes rotate-open {
    100% {transform: rotate(180deg);}
}

@keyframes rotate-open {
    100% {transform: rotate(180deg);}
}

@-webkit-keyframes rotate-close {
    100% {transform: rotate(360deg);}
}

@keyframes rotate-close {
    100% {transform: rotate(360deg);}
}

.navContainer .openNav {
    margin-top: 0px;
    -webkit-animation: nav-open 1s forwards;
            animation: nav-open 1s forwards;
}

.navContainer .closeNav {
    margin-top: 400px;
    -webkit-animation: nav-close 1s forwards;
            animation: nav-close 1s forwards;
}

@-webkit-keyframes nav-open {
    100% {margin-top: 400px;}
}

@keyframes nav-open {
    100% {margin-top: 400px;}
}

@-webkit-keyframes nav-close {
    100% {margin-top: 0px;}
}

@keyframes nav-close {
    100% {margin-top: 0px;}
}

.navContainer .back{
    background-color: none;
    -webkit-animation: nav-eas 0.3s forwards;
            animation: nav-eas 0.3s forwards;
}

.navContainer .without{
    background-color: #0e111b;
    -webkit-animation: nav-out 0.3s forwards;
            animation: nav-out 0.3s forwards;
}

@-webkit-keyframes nav-eas {
    100% {background-color: #0e111b;}
}

@keyframes nav-eas {
    100% {background-color: #0e111b;}
}

@-webkit-keyframes nav-out {
    100% {background-color: #ffffff00;}
}

@keyframes nav-out {
    100% {background-color: #ffffff00;}
}
